.Reviews {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: min-content min-content;
  grid-column-gap: 3rem;
  margin: 0 auto;
  width: 80vw;
  margin-bottom: 5rem;
}

.Reviews h1 {
  text-align: center;
  grid-row: 1/2;
  grid-column: 1 / 4;
  font-size: 4rem;
  font-weight: 300;
  font-style: italic;
  margin: 0;
  padding: 0;
  margin-bottom: 3rem;
  color: rgb(255, 255, 255);
}

.Review {
  display: flex;
  flex-direction: column;
  grid-row: 2 / 3;
  justify-items: center;
  align-items: center;
  justify-content: space-around;
  border: 1px solid #bdbdbd;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.8);
  background-color: rgba(255, 255, 255, 0.253);
  border-radius: 5px;
  color: #bdbdbd;
}

.Review ul {
  display: flex;
  width: 30%;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  margin-top: 1rem;
  align-items: center;
}

.Review p {
  letter-spacing: 1px;
  line-height: 20px;
  font-size: 1rem;
  font-weight: 400;
  text-align: center;
  padding: 0 4rem;
  margin-top: 1.2rem;
  justify-self: flex-end;
  margin-bottom: 2rem;
}

.Review img {
  margin-top: 2rem;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  align-self: center;
}

.Review .icon {
  fill: #ff005c;
}

@media (max-width: 75em) {
  .Review p {
    padding: 0 2rem;
  }
}

@media (max-width: 51.6em) {
  .Review p {
    padding: 0 2rem;
    font-size: 0.9rem;
    padding: 0 1rem;
  }
}

@media (max-width: 44em) {
  .Reviews {
    grid-template-columns: 80vw;
    grid-template-rows: repeat(auto-fit, 200px);
    margin: 0 auto;
    justify-self: center;
  }
  .Review {
    grid-row: unset;
    grid-column: 1 / 2;
    flex-wrap: wrap;
    height: 200px;
    flex-direction: row;
    margin-bottom: 2rem;
    display: grid;
    grid-template-columns: min-content 1fr;
    grid-template-rows: repeat(3, min-content);
  }
  .Review ul {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
    text-align: center;
    width: 50px;
    height: 50px;
    margin-left: 1rem;
  }
  .Review img {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    margin-left: 1rem;
  }
  .Review p {
    grid-row: 1 / 3;
    grid-column: 2 / 3;
    text-align: center;
    align-self: center;
    font-size: 1rem;
    padding: 0 2rem;
  }
  .Reviews h1 {
    grid-column: 1/ 2;
    font-size: 3.2rem;
  }
}

@media (max-width: 31.25em) {
  .Reviews h1 {
    font-size: 3rem;
    height: 150px;
  }
}

@media (max-width: 26.25em) {
  .Review p {
    font-size: 0.9rem;
    padding: 1rem;
  }
  .Review ul {
    text-align: start;
    margin: 0;
    padding: 1rem;
    width: 80px;
    height: 80px;
    align-self: flex-start;
  }
  .Review img {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    margin: 0;
    padding: 1rem;
  }
  .Review .icon {
    font-size: 2rem;
    text-align: start;
    align-self: flex-start;
  }
}
