.Results {
  display: grid;
  grid-template-columns: 25% 25% 25%;
  grid-template-rows: min-content;
  grid-column-gap: 8%;
  grid-row-gap: 3rem;
  margin: 0 auto;
  padding-bottom: 5rem;
  justify-content: center;
  height: max-content; /* ADDED AFTER CARDS WORKED ---  */
  min-height: 100vh;
}

.result {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content 1fr min-content min-content;
  grid-row-gap: 1rem;
  position: relative;
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.1),
    rgba(0, 0, 0, 0.2)
  );
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.75);
  transition: all 0.4s;
}

.result:hover {
  transform: scale(1.05);
}

.result .Image {
  grid-row: 1 / 2;
  grid-column: 1 / span 2;
  padding: 1rem;
}

.result img {
  width: 100%;
}

.result h1 {
  grid-row: 1 / 2;
  grid-column: 1 / span 2;
  text-align: center;
  position: absolute;
  bottom: -12%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #bdbdbd;
  opacity: 0.95;
  border-radius: 10px;
  padding: 5px 15px;
  font-style: italic;
  width: 80%;
}

.result .LocationPrice {
  grid-row: 3 / 4;
  grid-column: 1 / 2;
  text-align: center;
  color: white;
  font-size: 1.1rem;
  font-weight: 400;
  letter-spacing: 1px;
  padding: 0 0.5rem;
}

.result .FitsBeach {
  grid-row: 3 / 4;
  grid-column: 2 / 3;
  text-align: center;
  color: white;
  font-size: 1.1rem;
  font-weight: 400;
  letter-spacing: 1px;
  padding: 0 0.5rem;
}

.result .Price {
  grid-row: 4 / 5;
  grid-column: 1 / span 2;
  text-align: center;
  font-size: 1.2rem;
  color: #fff;
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
}

.result a {
  justify-self: center;
  text-align: center;
  grid-row: 5 / 6;
  grid-column: 1 / span 2;
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 300;
  padding: 0.5rem 1.2rem;
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.2),
    rgba(200, 200, 200, 0.8)
  );
  width: 50%;
  margin-bottom: 10px;
  border-radius: 50px;
  transition: all 0.2s;
}

.result a:hover {
  transform: translateY(-4px);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
}

.result a:active {
  transform: translateY(-2px);
}

.result span {
  grid-row: 2 / 3;
  grid-column: 1 / span 2;
  text-align: center;
  color: #ff005c;
  font-size: 1.2rem;
  margin: 0;
  padding: 0;
  padding-top: 15px;
}

@media (max-width: 75em) {
  .Results {
    grid-template-columns: 50vw;
    grid-template-rows: repeat(auto-fit, 300px);
    grid-row-gap: 2rem;
  }
  .result {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
  }
  .result .Image {
    grid-row: 1 / 5;
    grid-column: 1 / 4;
    text-align: center;
    justify-self: center;
    align-self: start;
    height: 100%;
    width: 100%;
    padding: 1rem;
    filter: brightness(0.4);
    z-index: 0;
  }
  .result .Image img {
    height: 90%;
    width: 90%;
    border-radius: 10px;
    object-fit: fill;
  }
  .result h1 {
    grid-row: 1 / 2;
    grid-column: 1 / 4;
    text-align: center;
    position: relative;
    background: none;
    background-image: radial-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.7));
    width: max-content;
    height: max-content;
    color: white;
    margin: 0;
  }
  .result span {
    grid-row: 1 / 2;
    grid-column: 1 / 4;
    text-align: center;
    padding-top: 3.5rem;
    height: max-content;
    z-index: 1;
  }
  .result .Price {
    grid-row: 2 / 3;
    grid-column: 1 / 4;
    text-align: center;
    font-size: 1.2rem;
    margin: 0;
    padding: 0;
    text-shadow: 2px 2px rgba(0, 0, 0, 1);
    z-index: 1;
  }
  .result .LocationPrice {
    grid-row: 3 / 4;
    grid-column: 1 / 4;
    text-align: center;
    margin-left: -10rem;
    z-index: 1;
  }
  .result .FitsBeach {
    grid-row: 3 / 4;
    grid-column: 1 / 4;
    text-align: center;
    margin-right: -10rem;
    z-index: 1;
  }
  .result a {
    grid-row: 4 / 5;
    grid-column: 1 / 4;
    text-align: center;
    z-index: 1;
    margin-bottom: 1rem;
    align-self: flex-start;
    background-image: none;
    background-color: rgba(255, 255, 255, 0.4);
  }
}

@media (max-width: 62.5em) {
  .Results {
    grid-template-columns: 60vw;
  }
}

@media (max-width: 43.75em) {
  .Results {
    grid-template-columns: 70vw;
  }
}

@media (max-width: 37.5em) {
  .Results {
    grid-template-columns: 80vw;
  }
}

@media (max-width: 31.25em) {
  .Results {
    grid-template-columns: 90vw;
  }
}
