.PaginationButtons {
  display: flex;
  margin: 0 auto;
  width: 20rem;
  justify-content: space-between;
  padding-bottom: 3rem;
}

.PrevBtn,
.NextBtn {
  display: inline-block;
  justify-self: center;
  font-size: 1.2rem;
  padding: 12px 25px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  border: 1px solid #379e62;
  color: #fff;
  font-style: italic;
  cursor: pointer;
  transition: all 0.2s;
}

.PrevBtn:hover,
.NextBtn:hover {
  transform: scale(1.1);
}
.PrevBtn:focus,
.NextBtn:focus {
  outline: none;
}
