.Header {
  display: grid;
  grid-template-columns: 10% 1fr 1fr 1fr 10%;
  grid-template-rows: 50vh 50vh;

  background-image: radial-gradient(
      rgba(0, 0, 0, 0.55),
      rgba(0, 0, 0, 0.7),
      rgba(0, 0, 0, 0.4)
    ),
    url(../../../assets/Hero.jpg);

  background-size: cover;
  background-position: center;
  margin: 0 auto;
  /* min-width: 100vw; */
}

.heading {
  margin-top: 5%;
  color: rgb(255, 255, 255);
  grid-column: 2 / 5;
  text-align: center;
  grid-row: 1 / 2;
}
.heading h1 {
  font-size: 4rem;
  font-weight: 300;
  position: relative;
  text-shadow: 2px 4px rgba(0, 0, 0, 0.4);
}

.TextHeading::after {
  content: "";
  border-bottom: 2px solid #bdbdbd;
  height: 2px;
  width: 50%;
  position: absolute;
  color: #fff;
  top: 125%;
  left: 24%;
}

.heading p {
  font-size: 2rem;
  font-weight: 400;
  text-shadow: 5px 2px 8px rgba(0, 0, 0, 0.6);
}
