.Navigation {
  position: absolute;
  color: #fff;
  width: 100%;
  border-bottom: 1px solid #bdbdbd;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 100;
  margin-top: 0;
}

.Navigation .Logo {
  position: absolute;
  top: 3%;
  left: 2%;
}

.Navigation .Logo img {
  width: 50px;
  height: 50px;
}

.NavigationList {
  display: flex;
  justify-items: center;
  align-items: center;
  justify-content: space-around;
  list-style: none;
  font-size: 1.2rem;
  font-weight: 400;
  font-family: "Lato", sans-serif;
}

.Navigation a {
  transition: all 0.5s;
  list-style: none;
  color: #fff;
  border: none;
  text-decoration: none;
}

.NavigationList a:hover {
  cursor: pointer;
  transform: translateY(-2px) scale(1.2);
  padding-bottom: 2px;
}

.MobileNavBtn {
  display: none;
  position: absolute;
}

@media only screen and (max-width: 50em) {
  .hidden {
    transform: translateX(-100%);
    transition: all 0.5s;
  }

  .MobileNavBtn {
    display: inline-block;
    height: 50px;
    width: 50px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    top: 3%;
    left: 3%;
    cursor: pointer;
    transition: all 0.2s;
  }
  .MobileNavBtn:hover {
    transform: scale(1.2);
  }

  .MobileNavBtn::before {
    content: "";
    display: inline-block;
    width: 50%;
    height: 2px;
    background-color: transparent;
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    border-top: 2px solid rgb(255, 255, 255);
    border-bottom: 2px solid rgb(255, 255, 255);
    padding: 3px 0;
    margin-top: 3px;
  }
  .MobileNavBtn::after {
    content: "";
    display: inline-block;
    width: 50%;
    height: 2px;
    background-color: rgb(255, 255, 255);
    position: absolute;
    bottom: 28%;
    left: 50%;
    transform: translateX(-50%);
  }

  .Navigation {
    width: 50%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.85);
    transition: all 0.4s;
  }
  .NavigationList {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-between;
  }
  .Navigation a {
    margin-top: 4rem;
    margin-right: 2rem;
  }
  .Navigation .Logo {
    display: none;
  }
}
