.Footer {
  background-color: #252525;
  color: #fff;
  padding: 0 2rem;
}

.Footer h3 {
  padding: 10px 0;
  text-align: center;
  font-size: 1rem;
  font-weight: 300;
  margin: 0;
}

.Footer ul {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 50%;
  list-style: none;
  font-style: italic;
  cursor: pointer;
  padding: 0;
  font-size: 0.9rem;
  font-weight: 300;
}

.Footer p {
  margin-top: 1rem;
  text-align: end;
  font-size: 0.7rem;
  margin-bottom: 0;
}
