.Listings {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 200px);
  margin: 2rem 4rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  position: relative;
  /* background-color: rgba(235, 235, 235, 0.5); */
  border-radius: 5px;
  /* border: 5px solid #ccc; */
  color: rgb(255, 255, 255);
  background-image: radial-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3)),
    url(../../../assets/ListingsBack.jpg);
  background-size: cover;
  background-position: center;
}

.Listings h2,
.Listings .para1 {
  letter-spacing: 1px;
  line-height: 25px;
}

.Listings h1 {
  font-size: 3.5rem;
  font-weight: 300;
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  text-align: center;
}

.Listings h2 {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  align-self: start;
  justify-self: center;
  font-style: italic;
}

.Listings .para1 {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  align-self: center;
  justify-self: center;
  padding: 0 10rem;
  font-size: 1.2rem;
  font-weight: 400;
  text-align: center;
}

.Listings a {
  grid-column: 2 / 3;
  grid-row: 3 / 4;
  align-self: start;
  justify-self: center;
  border: 2px solid #bdbdbd;
  cursor: pointer;
  outline: none;
  font-size: 1.5rem;
  border-radius: 100px;
  padding: 1rem 3rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.8);
  transition: all 0.2s;
  text-decoration: none;
  background-color: rgba(25, 185, 60, 0.8);
  border: 2px solid rgb(15, 112, 15);
  color: black;
}

.Listings a:hover {
  transform: translateY(-4px);
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.8);
}
.Listings a:active {
  transform: translateY(-2px);
}

.Listings .Image1,
.Listings .Image2,
.Listings .Image3 {
  width: 300px;
  height: 200px;
  background-position: center;
  background-size: cover;
  position: absolute;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.8);
}

.Listings .Image1 {
  background-image: url(../../../assets/FrontImage1.jpg);
  top: 12%;
  left: 28%;
  width: 20%;
  height: 300px;
  z-index: 1;
}

.Listings .Image2 {
  background-image: url(../../../assets/FrontImage2.jpg);
  top: 10%;
  left: 5%;
  width: 25%;
  z-index: 2;
}

.Listings .Image3 {
  background-image: url(../../../assets/FrontImage3.jpg);
  top: 45%;
  left: 8%;
  width: 35%;
  height: 300px;
}

@media (max-width: 94em) {
  .Listings .para1 {
    padding: 0;
  }
}

@media (max-width: 78.1em) {
  .Listings h1 {
    font-size: 3rem;
  }
  .Listings h2 {
    font-size: 1.4rem;
    padding: 0 1rem;
    text-align: center;
    margin-top: -1rem;
  }
  .Listings .para1 {
    font-size: 1rem;
    padding: 0 1rem;
  }
}

@media (max-width: 62.5em) {
  .Listings h1 {
    font-size: 2.5rem;
    padding: 1rem 2rem;
  }
  .Listings h2 {
    font-size: 1.2rem;
  }
}

@media (max-width: 46.9em) {
  .Listings {
    grid-template-rows: min-content min-content min-content min-content 300px;
    grid-template-columns: repeat(3, 1fr);
    margin: 1rem 1rem;
  }
  .Listings h1 {
    padding: 0 2rem;
    grid-row: 1 / 2;
    grid-column: 1 / 4;
    text-align: center;
  }
  .Listings h2 {
    font-size: 1.2rem;
    padding: 1rem 2rem;
    grid-row: 2 / 3;
    grid-column: 1 / 4;
    text-align: center;
  }
  .Listings .para1 {
    font-size: 1rem;
    margin-top: 9rem;
    grid-row: 3 / 4;
    grid-column: 1 / 4;
    margin-top: 0;
    text-align: center;
  }
  .Listings a {
    font-size: 1.2rem;
    align-self: center;
    grid-row: 4 / 5;
    grid-column: 2 / 3;
    padding: 10px 30px;
    margin-top: 1rem;
    width: 15rem;
    margin-bottom: 3rem;
  }
  .Listings .Image1,
  .Listings .Image2,
  .Listings .Image3 {
    width: 300px;
    height: 200px;
    background-position: center;
    background-size: cover;
    position: absolute;
    box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.8);
  }
  .Listings .Image1 {
    background-image: url(../../../assets/FrontImage1.jpg);
    top: 55%;
    left: 2%;
    width: 30%;
    height: 280px;
    z-index: 1;
  }

  .Listings .Image2 {
    background-image: url(../../../assets/FrontImage2.jpg);
    top: 60%;
    left: 70%;
    width: 25%;
    z-index: 2;
  }

  .Listings .Image3 {
    background-image: url(../../../assets/FrontImage3.jpg);
    top: 58%;
    left: 32%;
    width: 50%;
    height: 250px;
  }
}

@media (max-width: 35em) {
  .Listings .Image1,
  .Listings .Image2,
  .Listings .Image3 {
    width: 200px;
    height: 200px;
    background-position: center;
    background-size: cover;
    position: absolute;
    box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.8);
  }
  .Listings .Image1 {
    background-image: url(../../../assets/FrontImage1.jpg);
    top: 55%;
    left: 2%;
    width: 30%;
    height: 20%;
    z-index: 1;
  }

  .Listings .Image2 {
    background-image: url(../../../assets/FrontImage2.jpg);
    top: 75%;
    left: 60%;
    width: 35%;
    height: 20%;
    z-index: 2;
  }

  .Listings .Image3 {
    background-image: url(../../../assets/FrontImage3.jpg);
    top: 58%;
    left: 15%;
    width: 70%;
    height: 35%;
  }
  .Listings a {
    margin-bottom: 7rem;
  }
}

@media (max-width: 25em) {
  .Listings .Image1,
  .Listings .Image2,
  .Listings .Image3 {
    display: none;
  }
  .Listings a {
    margin-bottom: 1rem;
  }
  .Listings {
    grid-template-rows: min-content min-content min-content min-content;
  }
}
