.AboutUs {
  box-shadow: 0 0 10px black;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 6rem min-content;
  margin: 1rem 4rem;
  padding-bottom: 2rem;
  padding: 0 10rem;
  padding-bottom: 2rem;
  background-color: rgba(224, 224, 224, 0.9);
  border-radius: 10px;
  background-image: radial-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3)),
    url(../../../assets/AboutUsBack2.jpg);
  background-position: center;
  background-size: cover;
}

.AboutUs h1,
.AboutUs h2,
.AboutUs .para1,
.AboutUs .para2 {
  letter-spacing: 1px;
  line-height: 27px;
  color: #ffffff;
}

.AboutUs h1 {
  grid-column: 1 / span 2;
  grid-row: 1 / 2;
  text-align: center;
  font-size: 3rem;
  font-weight: 400;
  height: 50px;
  width: 50%;
  justify-self: center;
  font-style: italic;
}

.AboutUs .part1 {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  text-align: center;
  padding: 0 2rem;
}

.AboutUs .part1 h2,
.AboutUs .part2 h2 {
  font-size: 2rem;
}

.AboutUs .part1 p,
.AboutUs .part2 p {
  font-size: 1.2rem;
  font-weight: 300;
}

.AboutUs .part2 {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  text-align: center;
  padding: 0 2rem;
}

@media (max-width: 75em) {
  .AboutUs {
    padding: 0 1rem;
  }
}

@media (max-width: 56.25em) {
  .AboutUs {
    grid-template-rows: min-content min-content min-content;
  }

  .AboutUs h1 {
    margin-top: 1rem;
    margin-bottom: 0;
  }

  .AboutUs .part1 {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    text-align: center;
    padding: 0 2rem;
  }

  .AboutUs .part2 {
    grid-column: 1 / 3;
    grid-row: 3 / 4;
    text-align: center;
    padding: 0 2rem;
  }

  .AboutUs .part1 p,
  .AboutUs .part2 p {
    font-size: 1rem;
    font-weight: 300;
  }
}

@media (max-width: 46.8em) {
  .AboutUs {
    margin: 0.5rem 2rem;
  }
  .AboutUs h1 {
    font-size: 2.2rem;
  }
  .AboutUs .part1 h2,
  .AboutUs .part2 h2 {
    font-size: 1.6rem;
  }
  .AboutUs .part1 p,
  .AboutUs .part2 p {
    font-size: 0.9rem;
    font-weight: 300;
  }
}

@media (max-width: 25em) {
  .AboutUs {
    margin: 0.5rem 0.5rem;
  }
  .AboutUs h1 {
    font-size: 1.8rem;
  }
  .AboutUs .part1 h2,
  .AboutUs .part2 h2 {
    font-size: 1.2rem;
  }
  .AboutUs .part1 p,
  .AboutUs .part2 p {
    font-size: 0.6rem;
    font-weight: 300;
  }
  .AboutUs .para1,
  .AboutUs .para2 {
    letter-spacing: 0.8px;
    line-height: 22px;
    color: #d1d1d1;
  }
}
