.confirmModal {
  position: fixed;
  height: 700px;
  width: 600px;
  background-color: rgba(255, 255, 255, 0.95);
  border: 4px solid rgb(175, 175, 175);
  border-radius: 20px;
  text-align: center;
  z-index: 9999;
  left: 50%;
  top: 1%;
  transform: translateX(-50%);
  box-shadow: 0 0 50px 200vh rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: slideIn 0.5s ease-in;
}

.confirmModal h1 {
  font-size: 2rem;
  border-bottom: 1px solid black;
  padding-bottom: 20px;
  font-weight: 400;
  font-style: italic;
}

.confirmModal p {
  font-size: 1.2rem;
  letter-spacing: 1px;
  margin-bottom: 2rem;
  padding: 0 10px;
}

.CancelBtn,
.ConfirmBtn {
  padding: 20px 20px;
  color: white;
  width: 50%;
  border-radius: 10px;
  text-transform: uppercase;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.5);
  transition: all 0.2s;
  opacity: 0.9;
}

.CancelBtn {
  border: 3px solid rgb(95, 11, 8);
  background-color: rgba(185, 30, 25, 0.8);
  margin-bottom: 20px;
}

.ConfirmBtn {
  border: 3px solid rgb(15, 112, 15);
  background-color: rgba(25, 185, 60, 0.8);
  margin-bottom: 30px;
  margin-top: 50px;
}

.CancelBtn:hover,
.ConfirmBtn:hover {
  transform: scale(1.05) translateY(-4px);
  cursor: pointer;
}
.CancelBtn:focus,
.ConfirmBtn:focus {
  outline: none;
}

.CancelBtn:active,
.ConfirmBtn:active {
  transform: translateY(-2px);
}

@media only screen and (max-width: 43.75em) {
  .confirmModal {
    width: 75%;
  }
  .confirmModal h1 {
    font-size: 1.2rem;
  }
  .confirmModal p {
    font-size: 1rem;
  }
  .CancelBtn,
  .ConfirmBtn {
    width: 80%;
  }
}

@keyframes slideIn {
  0% {
  }
  100% {
  }
}
