.Booking {
  grid-column: 2 / 3;
  text-align: center;
  display: flex;
  flex-direction: column;
  border: 2px solid #bdbdbd;
  margin: 10px 10rem;
  background-color: rgba(0, 0, 0, 0.2);
  border: 2px solid #2e2e2e;
  border-radius: 10px;
  position: relative;
}

.Booking h5,
.Booking p {
  margin-left: 10rem;
}

.Booking h5 {
  font-size: 1.1rem;
}

.BookingTitle,
.NoBookings {
  font-size: 2rem;
  text-align: center;
  margin: 0;
  padding: 20px;
  font-style: italic;
  font-weight: 400;
}

.Booking img {
  width: 200px;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  border: 5px solid #2e2e2e;
  border-radius: 10px;
}
