.Error {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  background-color: salmon;
  border: 2px solid red;
  z-index: 2000;
  text-align: center;
  color: white;
  border-radius: 20px;
  box-shadow: 0 0 200px 200px rgba(0, 0, 0, 0.8);
  animation: slideIn 0.2s ease-in;
  font-style: italic;
  font-weight: 300;
}
.Success {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  background-color: rgba(25, 185, 60, 1);
  border: 2px solid rgb(15, 112, 15);
  z-index: 2000;
  text-align: center;
  color: white;
  border-radius: 20px;
  box-shadow: 0 0 200px 200px rgba(0, 0, 0, 0.8);
  animation: slideIn 0.2s ease-in;
  font-style: italic;
  font-weight: 300;
}

@keyframes slideIn {
  0% {
    transform: translateY(-100vh) translateX(-50%);
  }
  100% {
    transform: translateY(0) translateX(-50%);
  }
}
